.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup-content {
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  width: 95vw;
  height: 95vh;
  display: flex;
  flex-direction: column;
}

.fault-groups-container {
  flex-grow: 1;
  overflow-y: auto;
  width: 100%;
}

.fault-groups {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  justify-content: space-between;
  width: 100%;
}

.fault-group {
  border: 1px solid #ccc;
  padding: 10px;
  border-radius: 5px;
  width: calc(50% - 7.5px);
  box-sizing: border-box;
  margin-bottom: 15px;
}

.group-checkbox {
  margin-bottom: 10px;
}

.code-checkboxes {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.code-checkbox {
  display: flex;
  align-items: center;
  width: 100%;
}

.code-checkbox input[type="checkbox"] {
  margin-right: 5px;
  position: relative;
  z-index: 1;
}

.code-checkbox input[type="checkbox"]::before,
.code-checkbox input[type="checkbox"]::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
}

label {
  margin-left: 5px;
  cursor: pointer;
}

.exclude-faults-btn {
  background-color: #ce83b2;
  border: none;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 4px;
}

.exclude-faults-btn:hover {
  opacity: 0.8;
}

h2 {
  margin: 0 0 10px 0;
}

.fault-code-exclusion {
  margin: 10px 0;
}

.fault-groups-container::-webkit-scrollbar {
  width: 8px;
}

.fault-groups-container::-webkit-scrollbar-thumb {
  background-color: #888;
  border-radius: 4px;
}

.fault-groups-container::-webkit-scrollbar-track {
  background-color: #f1f1f1;
}

.control-buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  gap: 10px;
}

.showall-btn,
.excludeall-btn {
  background-color: #daeac0;
  border: none;
  color: black;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 4px;
}
.close-btn {
  padding: 5px 10px;
  background-color: #daeac0;
  border: none;
  border-radius: 10px;
  cursor: pointer;
}

.showall-btn:hover,
.excludeall-btn:hover,
.close-btn:hover {
  opacity: 0.8;
}

